.custom-modal {
    margin: 20%;
    margin-top: 10%;
    width: 60%;
    position: fixed;
    background-color: #fff;
    border: 1px solid #ddd;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    z-index: 9999;
    /* make sure it's on top of other elements */
    font-family: "poppins";
  }
  
  .clm1,
  .clm2,
  .clm3,
  .clm4 {
    flex-shrink: 0;
    text-align: center;
    width: 22%;
    /* adjust the width value to your liking */
  }
  
  .clm1,
  .clm3 {
    font-size: 20px;
    color: #FFF;
    font-weight: bold;
    background-color: rgba(5, 72, 167, 0.5);
  }
  
  .clm2,
  .clm4 {
    font-size: 20px;
    background: #F0EDFF;
  }
  
  .table-title{
    font-weight: bold;
    text-align: center;
  }
  
  .searchcontainer {
    display: flex;
    justify-content: center;
    z-index: 1;
    visibility: visible;
    font-family: "poppins";
  }
  
  .searchbox {
    position: relative;
    width: 30%;
    height: 80%;
    border-radius: 20px;
    align-self: center;
    display: grid;
    justify-content: center;
    margin-top: 3%;
    margin-bottom: 4%;
    z-index: 1;
    /* Ensure it's on top of any background layers */
  }
  
  .searchbox::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #E9E9E9;
    border-radius: 20px;
    opacity: 0.8;
    z-index: -1;
    /* Place behind the content */
  }
  
  .searchbox>* {
    position: relative;
    z-index: 1;
  }
  
  .input-container {
    display: grid;
  }
  
  .Vehiclelabel {
    font-size: 32px;
    font-weight: bold;
    margin: 20px;
    margin-top: 25px;
    text-align: center;
  }
  
  .vehicalenumberfield {
    text-align: center;
    margin: 20px;
    border-radius: 10px;
  
    width: 360px;
    height: 70px;
    flex-shrink: 0;
    border-radius: 15px;
    background: #ffffff;
    text-align: center;
    color: #000;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
  
  .vehicalenumberfield::placeholder {
    color: #000;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    opacity: 0.3;
    text-align: center;
  }
  
  .button-container {
    margin: 20px;
    margin-bottom: 60px;
  }
  
  .searchbutton1 {
    width: 360px;
    height: 70px;
    flex-shrink: 0;
    border-radius: 15px;
    background: linear-gradient(90deg, #021E45 0%, #054AAB 100%);
    color: #FFF;
    text-align: center;
    font-style: normal;
    line-height: normal;
    font-size: 28px;
    font-weight: bold;
  }
  
  .vehicle-search-bg::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("/public/images/components/BG4.jpg");
    background-size: cover;
    background-position: center;
    opacity: 0.6;
    z-index: -1;
 
  }
  
  .vehicle-search-bg>* {
    position: relative;
    z-index: 1;
  }